	module.exports = {"data":{"markdownRemark":{"html":"<p>It is a development support tool for EmEditor of script which works with \"freeware\" which is made by Mr. mi \"Toho Bulldog\". When installing this syntax file, the built-in functions, constants, etc. of Toho bullet style script will be emphasized, probably making programming easier than composing it in Notepad. With this syntax file, we support all scripts from stage scripts, single scripts, conversation events to self scripts and user defined graphics scripts. It conforms to Toho bullet style Version 0.12 m script. (It is a secret here only that it was a byproduct that was originally made on the scripting of the <a href=\"/!/thA/\">eastern part of the sealed castle</a> w)</p>\n<ul>\n<li>\n<p><a href=\"/filez/thd/EmDnh.zip\">Download \"</a> Bullet- <a href=\"/filez/thd/EmDnh.zip\">like style syntax file for EmEditor\"</a> ([filesize] <a href=\"https://danmaq.com/filez/thd/EmDnh.zip%5B/filesize\">https://danmaq.com/filez/thd/EmDnh.zip[/filesize</a> ])</p>\n</li>\n<li>\n<p><a href=\"http://www.emurasoft.com/jp/\">Obtain EmEditor</a></p>\n<h2>Change log</h2>\n</li>\n<li>\n<p>(November 8, 2006) Fixed that the built-in function \"DeleteEnemyShotImmediatelyInCircle\" was not highlighted</p>\n</li>\n<li>\n<p>(October 15, 2006) Fixed that the built-in instruction \"times\" and the built-in function \"GetPlayerScriptName\" were not emphasized, the built-in function \"ClearByteCodeCash\" was regarded as an old name and emphasized in another color</p>\n</li>\n</ul>","frontmatter":{"cover":null,"date":"2005-03-23T00:00:00+09:00","strDate":"2005/3/22","title":"Barrage style syntax file for EmEditor"}}},"pathContext":{"path":"/en/blog/2005/03/23-emeditor-danmakufu/","langKey":"en"}}

